body {
    position: relative;

    display: flex;

    min-height: 100vh;
    align-items: center;
    justify-content: center;
}

.range {
    width:500px;
    height:10px;

    border-radius: 4px;
    background-color: #ddd;
}

.range__button_1, .range__button_2 {
    position: absolute;
    top: 50%;

    width: 20px;
    height: 20px;

    cursor: move;
    transform: translateY(-50%);

    border: none;
    border-radius: 50%;
    background-color: #999;
}
.range__between
{
  position: absolute;
    height: 5px;
    width:0px;
    margin-top:3px;
    border: none;
    background-color: #559933;
}